.w-dateinput {
  display: inline-block;
}
.w-dateinput-popover {
  box-shadow: 0 0 0 0;
}
.w-dateinput.disabled .w-dateinput-close-btn {
  display: none;
}
.w-dateinput-close-btn {
  display: flex;
  margin: 0 3px;
  color: #a5a5a5;
  cursor: pointer;
}
.w-dateinput-close-btn:hover {
  color: #393e48;
}
